<template>
  <div class="iframe-opration">
    <div class="menu-top">
      <div class="left">
        <p class="title">{{editorMenu | filterMenu}}</p>
        <a :href="src" target="_blank">{{src}}</a>
      </div>
      <div class="right">
        <a :href="src" target="_blank"><el-button type="primary" class="btn">查看</el-button></a>
        <el-button type="default" class="btn" @click="editor" v-if="editorMenu !== '/detail'">编辑</el-button>
      </div>
    </div>
    <iframe :src="frameUrl" name="seller-preview"></iframe>
  </div>
</template>
<script>
  export default{
    data () {
      return {
        title: '首页',
        msg: 'hello vue',
        shopId: window.localStorage.shopId
      }
    },
    mounted () {
    },
    methods: {
      editor () {
        this.$router.push({
          path: '/pagerEditor',
          query: {url: this.$store.getters.editorMenu}
        })
      }
    },
    computed: {
      src () {
        let domainCompany = this.$store.getters.domainCompany
        let homepage = process.env.HOMEPAGE
        this.frameUrl = `${process.env.HOMEPAGE}${this.$store.getters.editorMenu}?shopId=${this.shopId}`
        if (domainCompany !== '' && domainCompany !== null && domainCompany !== undefined) {
          if (domainCompany.subdomain === '' || domainCompany.subdomain === null || domainCompany.subdomain === undefined) {    // 二级域名
          } else {
            homepage = 'http://' + domainCompany.subdomain + process.env.API_HOST
          }
        }
        let url = `${homepage}${this.$store.getters.editorMenu}`
        return url
      },
      editorMenu () {
        return this.$store.getters.editorMenu
      }
    },
    filters: {
      filterMenu (value) {
        let title = 'HOME'
        switch (value) {
          case '/':
            title = 'HOME'
            break
          case '/products':
            title = 'PRODUCTS'
            break
          case '/profile':
            title = 'COMPANY PROFILE'
            break
          case '/detail':
            title = 'PRODUCT DETAIL'
            break
          case '/contacts':
            title = 'CONTACTS'
            break
        }
        return title
      }
    }
  }
</script>
<style lang="less" rel="stylesheet/less" scoped>
  @import "../../../assets/style/variable";
  .iframe-opration{
    position: relative;
    float: right;
    width: calc(~"100% - 272px");
    .menu-top{
      border-radius: 4px;
      margin-top: 20px;
      padding: 20px;
      background: #fff;
      box-shadow:0 0 20px #DFE3EB;
      overflow:hidden;
      .left{
        display:inline-block;
        .title{
          font-size: 18px;
          font-weight: bold;
          margin-bottom: 10px;
        }
        a{
          color: @link;
          margin-top: 10px;
        }
      }
      .right{
        display:inline-block;
        float:right;
        margin-top: 8px;
        z-index:99;
        .btn{
          padding: 9px 25px;
        }
        .el-button{
          margin-left:16px;
        }
      }
    }
    iframe{
      margin-top: 20px;
      position: absolute;
      left: 50%;
      transform: translateX(-50%);
      width: calc(~"100% - 100px");
      height: 800px
    }
  }
</style>



