<template>
  <div class="left-menu">
    <el-menu default-active="1" class="el-menu-vertical-demo" @select="select" v-show="isSelect">
      <el-menu-item-group>
        <template slot="title"><span class="title">页面名称</span></template>
        <el-menu-item index="/" class="item">HOME</el-menu-item>
        <el-menu-item index="/products" class="item">PRODUCTS</el-menu-item>
        <!--<el-menu-item index="/detail" class="item">PRODUCT DETAIL</el-menu-item>-->
        <el-menu-item index="/profile" class="item">COMPANY PROFILE</el-menu-item>
        <el-menu-item index="/contacts" class="item">CONTACTS</el-menu-item>
      </el-menu-item-group>
    </el-menu>
  </div>
</template>
<script>
  export default{
    data () {
      return {
        isSelect: true,
        msg: 'hello vue'
      }
    },
    methods: {
      select (index) {
        this.$store.commit('EDITOR_MENU', {menu: index})
      }
    }
  }
</script>
<style lang="less" rel="stylesheet/less">
  @import "../../../assets/style/variable";
  .left-menu{
    margin-right: 20px;
    float: left;
    width: 250px;
    margin-top: 20px;
    .el-menu{
      border-radius: 4px;
      box-shadow:0 0 20px #DFE3EB;
    }
    .el-menu-item-group__title{
      background: #F5F8FA;
    }
    .title{
      font-size: 16px;
      //line-height: 40px;
      display: inline-block;
      padding-bottom: 10px;
      color: @base-color;
    }
    .item{
      height: 70px;
      line-height: 70px;
      background: #fff;
      border-top: 1px solid #DFE3EB;
    }
    .item.is-active {
      background: #F5F8FA;
    }
  }
  #logo-control{
    display: none;
  }
</style>



